import React, { Component } from "react";
import { $ } from 'react-jquery-plugin'

class Videos extends Component {
	loadVideos(videos) {
		const wrap = document.getElementById('videos-wrap');
		if (wrap.childElementCount !== 0)
			return;
		videos.forEach(e => {
			const iframe = document.createElement('iframe');
			iframe.src = "https://youtube.com/embed/" + e.id + "?controls=0&showinfo=0&rel=0";
			iframe.title = e.title;
			wrap.appendChild(iframe);
		})
		this.hideLoading();
	}
	hideLoading() {
		document.getElementById('videos-load').style.display = 'none';
	}
	render() {
		const CHANNEL_ID = 'UC-RVCefwipBS8WutREwbTmw';
		const REQUEST_URL = "https://www.youtube.com/feeds/videos.xml?channel_id=";
		
		$.getJSON("https://api.rss2json.com/v1/api.json?rss_url=" + encodeURIComponent(REQUEST_URL)+CHANNEL_ID, (data) => {
			const videos = [];
			data.items.forEach(e => {
				const link = e.link;
				const id = link.substr(link.indexOf("=")+1);
				videos.push({
					"title": e.title,
					"id": id
				});
			})
			this.loadVideos(videos);
		});

		return (<>
			<section>
                <h2>Vdeos</h2>

				<p>
					Els Arreplegats, des de fa temps, tenim un canal de <a href="https://www.youtube.com/channel/UC-RVCefwipBS8WutREwbTmw/" target="_blank" rel="noreferrer">YouTube</a> en el que hi pugem contingut divers relacionat amb la colla. Hi pots trobar els vdeos dels castells de cada actuaci, els vdeos promocionals de la colla o d'algun esdeveniment, canons de la famlia verd quirfan, i molt ms!
				</p>
				<p>
					A continuaci hi trobars els 10 ltims vdeos perqu comencis a nodrir-te d'essncia Arreplegada:
				</p>

				<div id="videos-load" className="loading" style={{marginTop: '2rem'}}></div>
				<div id="videos-wrap"></div>
			</section>
		</>);
	}
}

export default Videos;
