import React, { Component } from "react";
import { NavLink } from "react-router-dom";

class HistoriaDeLaColla extends Component {
	render() {
		return (<>
			<section className="historia">
				<h2>Histria de la colla</h2>

				<h5>Els inicis</h5>
				<p>
					Fundats a la primavera de 1995, els Arreplegats de la Zona Universitria vam ser la segona colla castellera universitria en aparixer. Ja des del primer moment vam apostar per noms castells complets, sempre amb acotxador i enxaneta. El mateix maig del 1995 es va carregar el primer pilar de quatre, i a inicis de l'any segent van arribar els primers castells de sis: 4 de 6 descarregat i 3 de 6 carregat.
				</p>
				<p>
					El 1997 es va fer el primer viatge a Perpiny, on finalment vam descarregar el primer 3 de 6 de la colla. Era un bon presagi, perqu l'objectiu de la temporada era el 5 de 6, que vam descarregar a la Diada el maig d'aquell mateix any, junt amb el primer pilar de 5 carregat.
				</p>
				<p>
					La temporada segent, vam experimentar un aven brutal, carregant i desprs descarregant la primera torre de 6, descarregant el tres de 6 per sota, i acabant l'xtasi amb el primer 4 de 7 carregat. Provar un castell de 7 pisos era un salt al desconegut, una nova dimensi en el mn casteller universitari. Aquell any tamb es va descarregar el primer pilar de 5, i es va aconseguir carregar del 3 de 6 net carregat, que obria les portes al 3 de 7.
				</p>
				<p>
					Aquest castell, el 3 de 7, es va fer esperar ms, sent el primer descarregat al final de la temporada 2000-2001. El segent any, es va carregar el primer 4 de 7 amb l'agulla, descarregat un any desprs. La temporada 2003-2004 va acabar amb dos nous castells: el <NavLink to="/castells/5d7">5 de 7</NavLink> i la torre de 7 amb folre, ambds descarregats. El segent castell amb folre va arribar durant la temporada 2006-2007, quan vam carregar el primer pilar de 6 amb folre de la histria castellera universitria. Aquest castell finalment es va descarregar a les acaballes de la temporada 2007-2008, en la Diada de Primavera dels Ganpies de l'Autnoma.
				</p>
			</section>
			<section className="image-divider img-576" style={{backgroundImage: `url('images/resized/3d8f-sant-albert-576_x_323.jpg')`}}></section>
			<section className="image-divider img-768" style={{backgroundImage: `url('images/resized/3d8f-sant-albert-768_x_431.jpg')`}}></section>
			<section className="image-divider img-992" style={{backgroundImage: `url('images/resized/3d8f-sant-albert-992_x_557.jpg')`}}></section>
			<section className="image-divider img-1200" style={{backgroundImage: `url('images/resized/3d8f-sant-albert-1200_x_674.jpg')`}}></section>
			<section className="image-divider img-1920" style={{backgroundImage: `url('images/resized/3d8f-sant-albert-1920_x_1079.jpg')`}}></section>
			<section className="image-divider img-max" style={{backgroundImage: `url('images/3d8f-sant-albert.jpg')`}}></section>
			<section className="historia">
				<h5>Els nics que ho poden fer</h5>
				<p>
					La temporada 2008-2009 va estar marcada pel domini absolut de la gamma de castells de set i de set i mig, duts a diverses places. Aquest alt nivell va permetre la colla assolir a finals de temporada l'xit ms sonat, descarregant en la mateixa actuaci dos castells universitaris indits: el <NavLink to="/castells/3d7s">3 de 7 per sota</NavLink> i el <NavLink to="/castells/4d8">4 de 8</NavLink>. Aquest ltim es va convertir en el primer castell de vuit pisos fet mai per una colla castellera universitria.
				</p>
				<p>
					La gran revoluci va venir el 2010, quan ens atrevem a portar a plaa les primeres manilles universitries de la m del <NavLink to="/castells/Pd7fm">pilar de 7 amb folre i manilles</NavLink>. El vam haver de deixar en carregat, per no ens vam rendir, i el maig del 2013 el descarregvem finalment.
				</p>
				<p>
					No gaire ms tard, l'hivern del 2014, vam portar a la Diada dels Ganpies de la UAB el primer <NavLink to="/castells/3d8f">3 de 8 amb folre</NavLink> del mn universitari, que veia com es feia histria un altre cop en descarregar aquest castell fins aleshores indit. Aquell mateix desembre, pel nostre 19 Aniversari, sorprenem el mn sencer obrint plaa amb el <NavLink to="/castells/Vd6f">vano de 6</NavLink>.
				</p>
			</section>
			<section className="image-divider img-576" style={{backgroundImage: `url('images/resized/pd7fm-construccio-2022-576_x_383.jpg')`}}></section>
			<section className="image-divider img-768" style={{backgroundImage: `url('images/resized/pd7fm-construccio-2022-768_x_512.jpg')`}}></section>
			<section className="image-divider img-992" style={{backgroundImage: `url('images/resized/pd7fm-construccio-2022-992_x_661.jpg')`}}></section>
			<section className="image-divider img-1200" style={{backgroundImage: `url('images/pd7fm-construccio-2022.jpg')`}}></section>
			<section className="image-divider img-1920" style={{backgroundImage: `url('images/pd7fm-construccio-2022.jpg')`}}></section>
			<section className="image-divider img-max" style={{backgroundImage: `url('images/pd7fm-construccio-2022.jpg')`}}></section>
			<section className="historia">
				<h5>L'poca d'or</h5>
				<p>
					La segent temporada els Arreplegats vam seguir escribint la histria dels castells. El maig de 2015, en la nostra 20a Diada, vam descarregar el primer <NavLink to="/castells/9d7">9 de 7</NavLink> universitari que mai ning ha tornat a intentar.
				</p>
				<p>
					Aquests anys vam dominar tots els grans castells. Podiem fer, amb relativa facilitat, els castells ms complicats del mn casteller universitari, castells que ning ha pogut igualar encara. Vam passejar per tot arreu els ja esmentats <NavLink to="/castells/3d8f">3 de 8 amb folre</NavLink>, <NavLink to="/castells/3d7s">3 de 7 per sota</NavLink>, <NavLink to="/castells/5d7">5 de 7</NavLink>, i <NavLink to="/castells/4d8">4 de 8</NavLink> demostrant el nostre domini per totes les places on passvem.
				</p>
				<p>
					Aquesta poca gloriosa va assolir el seu punt ms lgid el 5 de maig del 2016, on vam juntar el <NavLink to="/castells/3d8f">3 de 8 amb folre</NavLink>, el <NavLink to="/castells/4d8">4 de 8</NavLink>, i vam carregar la indita <NavLink to="/castells/Td8fm">torre de 8 amb folre i manilles</NavLink> en una diada histrica.
				</p>
			</section>
			<section className="image-divider img-576" style={{backgroundImage: `url('images/resized/3d8f-sentiment-2022-576_x_418.jpg')`}}></section>
			<section className="image-divider img-768" style={{backgroundImage: `url('images/resized/3d8f-sentiment-2022-768_x_557.jpg')`}}></section>
			<section className="image-divider img-992" style={{backgroundImage: `url('images/resized/3d8f-sentiment-2022-992_x_720.jpg')`}}></section>
			<section className="image-divider img-1200" style={{backgroundImage: `url('images/resized/3d8f-sentiment-2022-1200_x_871.jpg')`}}></section>
			<section className="image-divider img-1920" style={{backgroundImage: `url('images/resized/3d8f-sentiment-2022-1920_x_1394.jpg')`}}></section>
			<section className="image-divider img-max" style={{backgroundImage: `url('images/3d8f-sentiment-2022.jpg')`}}></section>
			<section className="historia">
				<h5>Represa desprs de la COVID-19</h5>
				<p>
					En reprendre l'activitat castellera desprs del confinament, hi havia molta feina a fer. Renovaci, formaci, recaptaci de nous castellers, etc. Va ser un procs llarg per no per aix el vam encarar amb menys illusi.
				</p>
				<p>
					La diada ms important des d'aleshores ha estat el nostre 27 Aniversari, el 22 de desembre de 2022. Desprs d'una gran temporada amb molta formaci, vam poder conrear els fruits de tota la feina feta, i vam fer-hi el primer <NavLink to="/castells/5d7">5 de 7</NavLink> universitari post-COVID, el primer <NavLink to="/castells/3d8f">3 de 8 amb folre</NavLink> universitari post-COVID, la nostra quarta torre de 7 amb folre de la temporada, i hi vam carregar el <NavLink to="/castells/Pd7fm">pilar de 7 amb folre i manilles</NavLink>. Aquest pilar no noms representava les primeres manilles universitries post-COVID, sin que tamb ens va convertir en la sisena colla global en fer unes manilles desprs de la pandmia.
				</p>
			</section>
		</>);
	}
}

export default HistoriaDeLaColla;
